@font-face {
  font-family: 'Indie Flower';
  font-style: normal;
  font-weight: 400;
  src: url('../assets/fonts/indie-flower-v11-latin-regular.eot');
  /* IE9 Compat Modes */
  src: local('Indie Flower'), local('IndieFlower'),
    url('../assets/fonts/indie-flower-v11-latin-regular.eot?#iefix')
      format('embedded-opentype'),
    /* IE6-IE8 */ url('../assets/fonts/indie-flower-v11-latin-regular.woff2')
      format('woff2'),
    /* Super Modern Browsers */
      url('../assets/fonts/indie-flower-v11-latin-regular.woff') format('woff'),
    /* Modern Browsers */
      url('../assets/fonts/indie-flower-v11-latin-regular.ttf')
      format('truetype'),
    /* Safari, Android, iOS */
      url('../assets/fonts/indie-flower-v11-latin-regular.svg#IndieFlower')
      format('svg');
  /* Legacy iOS */
}

