.goal {
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
}

.goalLogo {
  display: flex;
  text-align: center;
  align-items: center;
}

.goalName {
  font-family: 'Montserrat';
  font-weight: bold;
  color: #858598;
  font-size: 12px;
  line-height: 32px;
  margin: 0;
  width: 75px;
}

.goalBtn {
  display: block;
  font-family: 'Montserrat';
  font-weight: bold;
  text-transform: uppercase;
  min-width: 180px;
  height: 32px;
  line-height: 32px;
  background-color: #e0e0e0;
  border-radius: 16px;
  margin: 0 20px;
  border: none;
  pointer-events: none;
  padding: 0 15px;
}

.goalBtnActive {
  display: block;
  font-family: 'Montserrat';
  font-weight: bold;
  text-transform: uppercase;
  min-width: 180px;
  height: 32px;
  line-height: 32px;
  border-radius: 16px;
  margin-left: 10px;
  padding: 0px 15px;
  margin: 0 20px;
  border: none;
  background: linear-gradient(
    rgb(236, 177, 70) 0%,
    rgb(241, 195, 65) 55%,
    rgb(245, 213, 59) 100%
  );
  box-shadow: 0px 8px 29px 0px rgba(178, 138, 61, 0.31);
  transform: scale(1.05);
  transition: all 0.5s ease-out;
  outline: none;
}

.goalBtnActive::before {
  display: block;
  color: #000;
  content: 'Натисни мене !';
  position: absolute;
  transform: translate(-100px, 50px);
  width: 85px;
}

.goalBtnActive::after {
  display: block;
  width: 85px;
  height: 45px;
  background-image: url(../../assets/images/arrow.png);
  background-size: contain;
  background-repeat: no-repeat;
  margin-top: 10px;
  transform: translateX(-100px) scale(-1, 1);
}

.progresBar {
  display: flex;
  align-items: center;
  min-width: 255px;
}

.progresScore {
  color: #858598;
  font-family: 'Montserrat';
  font-weight: 700;
  font-size: 14px;
  width: 100px;
}

.total {
  color: #000;
}

@media screen and (max-width: 1279px) {
  .progress {
    margin: 0 auto;
  }
}

@media screen and (max-width: 767px) {
  .progress {
    max-width: 180px;
  }
  .goalName {
    display: none;
  }
}
